.clockBox {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;

    border: solid hsla(0, 0%, 0%, 0.144) 0.5px;
    border-radius: 8px;
    box-sizing: border-box;

    box-shadow: rgba(0, 0, 0, .2) 15px 28px 25px -18px;
    
    padding: 10px;
    height: 170px;
    width: 150px;
    
    color: #ffffffb2;
    background: rgba(5, 5, 5, 0.4);
    background-position: 0 90%;
    letter-spacing: 0.5px;

    transition: all 235ms ease-in-out;

    cursor: pointer;
}

.clockBox:hover {
    background-color: hsla(0, 0%, 0%, 0.116);
    border: solid hsla(0, 0%, 0%, 0.555) 0.5px;
    box-shadow: rgba(0, 0, 0, .4) 0px 8px 8px -3px;
    transform: translate3d(4px, 2px, 0);
}

