@import url('https://fonts.googleapis.com/css2?family=Cute+Font&family=Montserrat:ital,wght@0,100;0,300;0,400;1,100;1,300;1,400&family=Quicksand:wght@300;400;700&display=swap');

* {
  margin: 0;
  font-family: 'Cute Font', sans-serif;
}

body {
  background-color: #6366f2;
  background: url(./img/cool-background.png) no-repeat center center fixed;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  transition: all 235ms ease-in-out;
}


